'use client';
import Image from 'next/image';
import Link from 'next/link';
import {motion} from 'framer-motion';
import {format} from 'date-fns';
import {Post} from '@/lib/blog';
import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
  CardTitle,
  CardDescription,
} from '@/components/ui/card';
import {Avatar, AvatarFallback, AvatarImage} from '@/components/ui/avatar';
import {Button} from '@/components/ui/button';

const cardVariants = {
  hidden: {opacity: 0, y: 20},
  visible: {opacity: 1, y: 0},
};

export function BlogPostCard({
  slug,
  title,
  excerpt,
  date,
  author,
  image,
  imageHint,
}: Post) {
  const formattedDate = format(new Date(date), 'PPP');

  return (
    <motion.div variants={cardVariants}>
      <Card className="group flex h-full transform flex-col overflow-hidden bg-surface transition-all duration-300 hover:shadow-lg hover:shadow-black/40">
        <Link href={`/blog/${slug}`} className="block">
          <CardHeader className="overflow-hidden p-0">
            <Image
              src={image}
              alt={title}
              width={800}
              height={450}
              className="h-auto w-full object-cover transition-transform duration-300 group-hover:scale-105"
              data-ai-hint={imageHint}
            />
          </CardHeader>
        </Link>
        <CardContent className="flex-grow p-6">
          <CardTitle className="text-xl font-bold leading-tight">
            <Link href={`/blog/${slug}`}>{title}</Link>
          </CardTitle>
          <CardDescription className="mt-2">{excerpt}</CardDescription>
        </CardContent>
        <CardFooter className="flex items-center justify-between p-6 pt-0">
          <div className="flex items-center gap-3">
            <Avatar className="h-10 w-10">
              <AvatarImage src={`https://i.pravatar.cc/40?u=${author}`} />
              <AvatarFallback>
                {author
                  .split(' ')
                  .map(n => n[0])
                  .join('')}
              </AvatarFallback>
            </Avatar>
            <div>
              <p className="font-semibold">{author}</p>
              <p className="text-sm text-muted-foreground">{formattedDate}</p>
            </div>
          </div>
          <Button asChild variant="link" size="sm">
            <Link href={`/blog/${slug}`}>Read More</Link>
          </Button>
        </CardFooter>
      </Card>
    </motion.div>
  );
}
